/* Search bar */

.search {
  display: inline-block;
  vertical-align: middle;
  position: absolute;
  right: 0;
  margin: auto;
  top: 10%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 100%;
  text-align: center;
}
.search_bar {
  width: 280px;
  position: absolute;
  margin: 0 auto;
  right: 6px;
  background: transparent;
}
.search_bar input[type=text] {
  width: 15px;
  background: transparent;
  transition: border 0.3s 0s, width 0.2s 0.3s cubic-bezier(0.225, 0.01, 0.475, 1.01), text-indent 0.2s 0.3s;
  padding: 5px;
  border-color: #FFF;
  text-indent: 30px;
  outline: none;
  border: 0px solid #FFF;
  font-size: 13px;
  color: #FFF;
  border-radius: 2px;
}
.search_bar ::-webkit-input-placeholder {
  /* Safari, Chrome and Opera */
  color: #FFF;
  font-weight: 400;
  font-family: "Gotham Book", sans-serif;
}
.search_bar ::-moz-input-placeholder {
  /* Safari, Chrome and Opera */
  color: #FFF;
  font-weight: 400;
  font-family: "Gotham Book", sans-serif;
}
.search_bar ::-o-input-placeholder {
  /* Safari, Chrome and Opera */
  color: #FFF;
  font-weight: 400;
  font-family: "Gotham Book", sans-serif;
}
.search_bar input[type=checkbox] {
  display: none;
}
.search_bar input[type=checkbox]:checked + label + input {
  width: 230px;
  border: 2px solid #FFF;
  text-indent: 0px;
}
.search_bar input[type=checkbox]:checked + label i {
  right: 0px;
  -webkit-transform: translateY(-50%) translateX(-200%) rotate(360deg) scale(0);
          transform: translateY(-50%) translateX(-200%) rotate(360deg) scale(0);
  color: #FFF;
}
.search_bar input[type=checkbox]:not(checked) + label i {
  right: 10%;
  top: 42%;
  color: #FFF;
}

.search_bar input[type=checkbox]:checked + label .last {
  left: 210px;
  top: 22px;
  -webkit-transform: translateY(-50%) rotate(360deg) scale(1);
          transform: translateY(-50%) rotate(360deg) scale(1);
}
.search_bar input[type=checkbox]:checked + label p {
  top: 40%;
  transition: all 0.3s 0.45s;
}
.search_bar input[type=checkbox]:not(checked) + label p {
  top: -45%;
  transition: all 0.3s 0s;
}
.search_bar .last {
  -webkit-transform: translateY(-50%) rotate(0deg) scale(0);
  transform: translateY(-50%) rotate(0deg) scale(0);
}
.search_bar i {
  position: absolute;
  font-size: 30px;
  top: 45%;
  -webkit-transform: translateY(-50%) translateX(50%) rotate(0deg) scale(1);
          transform: translateY(-50%) translateX(50%) rotate(0deg) scale(1);
  cursor: pointer;
  z-index: 2;
  margin: auto;
  border-radius: 4px;
  width: 56px;
  right: 50%;
  height: 54px;
  background: transparent;
  transition: right 0.3s 0.3s, color 0.3s, -webkit-transform 0.3s 0.3s;
  transition: right 0.3s 0.3s, transform 0.3s 0.3s, color 0.3s;
