/* Movieboard and movies */

.movie-board{
  position: absolute;
  top: 10%;
  left: 4%;
  right: 4%;
  margin-bottom: 100px;
  display:grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.movie-container{
  padding: 10px;
  -webkit-perspective: 100px;
}

.movie-container img{
  display: block;
  height: 400px;
  width: auto;
  max-width: 300px;
}

.movie-container:hover img{
  height: 450px;
  -webkit-transform: translateZ(6px);
          transform: translateZ(6px); 
  cursor: pointer;
  transition: all 0.3s;
}

.movie-container i{
  display: none;
  z-index: 2;
  position: fixed;
  color: #fff;
  margin: 0px;
  background: transparent;
  font-size: 63px;
  -webkit-transform: translate(414%, -57%);
          transform: translate(414%, -57%);
