/* Trailer */

.trailer-box{
  position: fixed;
  padding: 8vh 2vh 23vw 5vh;
  background: rgba(2,2, 2, 0.9);
  top: 8%;
  display: grid;
  grid-template-columns: 3fr 2fr;
}

.movie-info{
  position: relative;
  background: transparent;
}

.movie-info p,h1,h2{
  padding-bottom: 4vh;
  background: transparent;
  color: #FFF;
}

.trailer-holder{
  position: relative;
}

.movie-info #actors{
  position: relative;
  background: transparent;
  text-align: center;
}

.movie-info #actors > h2, p{
  vertical-align: top;
  display: inline-block;
}

.trailer-box .menu{
  position: relative;
  grid-column: 1 / span 2;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  bottom: 4;
  left: 0;
  right: 0;
  width: 100%;
  font-size: 1vw;
  z-index: 100;
  padding: 0;
  margin: 0;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  list-style: none;
  pointer-events: none;
}

.trailer-box .menu li{
  padding: 1em 1.5em 0;
  cursor: pointer;
  color: #ccc;
  min-width: 5%;
  pointer-events: auto;
}

.tile__img{
  width:280px;
  height: (280 / (16/9))px;
